import TcpServer from './server/tcp';
import MQTTServer from './server/mqtt';
import EnvConfig from './config/config';

const PORT_MQTT = 8500;
const HOST_MQTT = "0.0.0.0";

const PORT_TCP = 8501;
const HOST_TCP = "0.0.0.0";

EnvConfig();

async function bootraps() {
    
    Promise.allSettled([
        MQTTServer(PORT_MQTT, HOST_MQTT),
        TcpServer(PORT_TCP, HOST_TCP),
    ])
}

bootraps();